import represent from "repr.js";

const window = {
  addEventListener: function() {
    console.log("something called window.addEventListener");
  }
};

function repr(v) {
  if (typeof v === "symbol") {
    return v.toString();
  }
  return represent.repr(v);
}

let lib = {};
const state = {};

onmessage = function(e) {
  const {data: {load: data, output, stdlib}} = e;
  try {
    const thunk = eval(data);
    if (stdlib) {
      lib = thunk();
      return;
    }
    if (typeof thunk === "function") {
      thunk(state, lib);
      if (output) {
        postMessage(["result", repr(state.exports.replOut)]);
      }
    }
  } catch(e) {
    console.error(e);
    postMessage(["error", `${e.name}: ${e.message}`]);
  }
};



